import React, { useState } from 'react';
import { Button, Card, Col, Container, Form, Row, InputGroup } from 'react-bootstrap';
import Axios from 'axios';
import { APIRegister } from 'src/api/APIRegister';
import { Link } from 'react-router-dom';

const Signup = () => {
  const [nameReg, setNameReg] = useState('');
  const [emailReg, setEmailReg] = useState('');
  const [passwordReg, setPasswordReg] = useState('');
  const [contactNoReg, setContactNoReg] = useState('');
  const [addressReg, setAddressReg] = useState('');
  const statusReg = "1";
  const roleReg = "1";

  const [successMessage, setSuccessMessage] = useState('');
  const [errorMessage, setErrorMessage] = useState('');

  const register = () => {
    APIRegister().REGISTER({
      name: nameReg,
      email: emailReg,
      password: passwordReg,
      contact: contactNoReg,
      address: addressReg,
      status: statusReg,
      role: roleReg
    }).then((response) => {
      console.log(response);
      
      setSuccessMessage('Registered successfully!');
    }).catch((error) => {
      console.error("Please fill all details !!:", error);
      setErrorMessage('Please fill all details !!');
    });
  };

  return (
    <Container className="d-flex justify-content-center mt-5">
      <Row className="w-100">
        <Col md={6} lg={5} className="mx-auto">
          <Card className="p-4" id='card_border_color_change'>
            <Form>
              <h1 className="text-center">Sign Up now</h1>
              <InputGroup className="mb-3">
                <Form.Control
                  value={nameReg}
                  onChange={(e) => setNameReg(e.target.value)}
                  type="text"
                  placeholder="Name"
                />
              </InputGroup>
              <InputGroup className="mb-3">
                <Form.Control
                  value={emailReg}
                  onChange={(e) => setEmailReg(e.target.value)}
                  type="email"
                  placeholder="Email"
                />
              </InputGroup>
              <InputGroup className="mb-3">
                <Form.Control
                  value={passwordReg}
                  onChange={(e) => setPasswordReg(e.target.value)}
                  type="password"
                  placeholder="Password"
                />
              </InputGroup>
              <InputGroup className="mb-3">
                <Form.Control
                  value={contactNoReg}
                  onChange={(e) => setContactNoReg(e.target.value)}
                  type="tel"
                  placeholder="Contact"
                />
              </InputGroup>
              <InputGroup className="mb-3">
                <Form.Control
                  value={addressReg}
                  onChange={(e) => setAddressReg(e.target.value)}
                  type="text"
                  placeholder="Address"
                />
              </InputGroup>
              <Row className="mt-3">
                <Col className="text-center">
                  <Button onClick={register} className="px-4" id='but_color'>
                    Register
                  </Button>
                  {errorMessage && <div className="text-danger mt-2">{errorMessage}</div>}
                  {successMessage && <div className="text-success mt-2">{successMessage}</div>}

                </Col>
              </Row>
              <Row className="mt-3">
                <Col className="text-center">
                  <Link to={'/'}>
                    <Button className="px-4" id='but_color'>
                      Go for Login
                    </Button>
                  </Link>

                </Col>
              </Row>

            </Form>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default Signup;
